import React, { useEffect, useContext, useState } from 'react'
import { IoIosPlayCircle } from 'react-icons/io';

import Box from 'components/Box';
import Flex from 'components/Flex';
import Text from 'components/Text';
import Image from 'components/Image';
import Button from 'components/Button';
import FullpageVerticalCenter from 'components/FullpageVerticalCenter'
import Link from 'i18n/Link'

import AnswerContext from '../../AnswerContext'

import resultman from './resultman.svg'
import Layout from '../Layout'
import request from '../../utils/request'

const levels = [
  '前', '中', '後'
]

const Result = () => {
  const { answers, clearAnswer } = useContext(AnswerContext)
  const [answerClone, cloneAnswer] = useState([])
  useEffect(() => {
    if (answers && answers.length) {
      cloneAnswer(answers)
      request('https://us-central1-re-drive-75970.cloudfunctions.net/app/health-survey', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(answers)
      }).then(clearAnswer)
    }
  })

  const points = answerClone.reduce((res, a, i) => {
    const s = Math.floor(i / 3)
    res[s] = (res[s] || 0) + +a
    return res
  }, [])

  const min = Math.min(...points)

  return (
    <Layout height="100vh" bg="blue">
      <FullpageVerticalCenter>
        <Box textAlign="center" color="white">
          <Text fontSize="3.4375em" fontWeight="bold">你的各階段分數</Text>
          <Box fontSize="1.125em">
            <Text>結果的重點在於三個階段分數的「相對差異」</Text>
            <Text>檢視你在溝通中最容易忽略的環節</Text>
            <Text>點擊下方按鈕，看看為你準備的專屬溝通建議</Text>
          </Box>
        </Box>
        <Box
          mt="1.25em"
          mx="auto"
          py="1.25em"
          bg="white"
          width="56.88%"
          borderRadius="1.5em"
          textAlign="center"
          position="relative"
        >
          {levels.map((level, index) => (
            <Box fontSize="2em" py="1.25rem">溝 通 {level}
             <Box.inline
               mx="1rem"
               px="3.875rem"
               py="1rem"
               fontWeight="bold"
               bg="darkGray"
               color={min === points[index] ? 'yellow' : "blue"}
               fontSize="2.625rem"
               borderRadius="0.5rem"
               lineHeight="1"
             >
               {points[index]}
             </Box.inline>
             分
            </Box>
          ))}
          <Box width="15.8%" position="absolute" bottom="0" left="-4.7%">
            <Image src={resultman} />
          </Box>
        </Box>
        <Box textAlign="center" mt="1.25em">
          <Button
            px="1.25em"
            borderRadius="1.5em"
            rightIcon={IoIosPlayCircle}
            is={Link}
            to="/tutorial"
          >看看你最需要的溝通建議</Button>
        </Box>
      </FullpageVerticalCenter>
    </Layout>
  )
}

export default Result
